import React from 'react'
import { useSelector } from 'react-redux'
import { useDimension } from 'Page/StockEntry/GeneralServices/'
import { Link } from 'react-router-dom'

const { height, emptyItem, width } = useDimension(90)

const Settings = (props) => {
    const heightAdj = height - 70
    const version = useSelector(state => state.version)
    return (
        <div className="d-flex flex-column justify-content-between" style={{ minHeight: height, height: height, maxHeight: height }} >
            <div className='bg-white border radius-all ' style={{ minHeight: heightAdj, height: heightAdj, maxHeight: heightAdj }}>
                <div className='h4 m-0 p-3 text'>System Settings</div>

                {/* {Active Stock} */}
                <Link tabIndex='0' className='sku-item' to='/settings/active-stock'>
                    <div className='border-top border-bottom p-3 d-flex justify-content-between'>
                        <div>
                            <span className=' LatoBold text'>Active Stock</span>
                            {/* <span className=' LatoBold font-green'>{` (Last Sync:${'19:00 - 30-03-2021)'}`}</span> */}
                        </div>
                        <div className='iconU-arrowRight text' />
                    </div>
                </Link>
            </div>
            <div className='text pl-3 pb-3'>
                <span style={{ fontSize: '1rem' }} >{`Version : `}</span> <span style={{ fontSize: '1rem' }} className='font-green'>{window.config.FE_VERSION}  | {`${version ? version : '-'} BE`}</span>
            </div>
        </div >
    )
}

export default Settings